<template>
    <div class="addressWarp">
        <div class="rating_page">
        <header>
            <router-link to="/info">
                <i class="el-icon-arrow-left"></i>
            </router-link>
            <span class="header_span1">编辑地址</span>
            <span class="header_span2" @click="isShow1 = !isShow1">{{isShow1 ? '完成' : '编辑'}}</span>
        </header>
        <div class="address">
            <ul class="address_ul">
                <li class="address_ul_li" v-for="(a,index) in address">
                    <div>
                        <p class="address_ul_li_p">{{a.address_detail}}</p>
                        <p class="address_ul_li_p">
                            <span class="address_ul_li_s1">{{a.phone}}</span>
                        </p>
                    </div>
                    <div class="deletesite" v-if="isShow1" @click="deleteAddress(a,index)">
                        <span>X</span>
                    </div>
                </li>
            </ul>
            <router-link to="/add">
                <div class="addsite">
                    <span class="addsite_s1">新增地址</span>
                    <span class="addsite_s2">
                        <i class="el-icon-arrow-right"></i>
                    </span>
                </div>
            </router-link>
        </div>
    </div>
    </div>
</template>

<script>
    export default {
        name: "Address",
        data(){
            return {
                address:"",
                user_id:"",
                address_id:"",
                isShow1:false,
                isShow2:true,
            }
        },
        created(){
            this.user_id = JSON.parse(localStorage.getItem('user_id'));
            this.axios.get("/v1/users/" + this.user_id + "/addresses").then(res=>{
                console.log(res);
                this.address = res;
            }).catch(err=>{});
        },
        methods:{
            deleteAddress(a,index){
                this.address_id = a.id
                this.axios.delete("/v1/users/" + this.user_id + "/addresses/"+ this.address_id).then(res=>{
                    // console.log(res);
                    this.address.splice(index,1);
                }).catch(err=>{});

            }
        }
    }
</script>

<style scoped>
    .addressWarp{
        width: 100%;
        overflow: hidden;
    }
    a{
        text-decoration: none;
    }
    header>a{
        color: #fff;
        position: absolute;
        top: 50%;
        left: .4rem;
        transform: translateY(-50%);
    }
    .header_span1{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }
    .header_span2{
        right: .4rem;
        font-size: .7rem;
        color: #fff;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
    }

    .rating_page{
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 202;
        background: #f2f2f2;
        padding-top: 1.95rem;
    }
    .address{
        width: 100%;
        margin-top: .4rem;
        border-top: 1px solid #d9d9d9;
        border-bottom: 1px solid #d9d9d9;
    }
    .address_ul{
        background: #fff;
    }
    .address_ul_li{
        background: #fff8c3;
        border-bottom: 1px solid #d9d9d9;
        padding: .4rem;
        display: flex;
        justify-content: space-between;
    }
    .address_ul_li_p{
        line-height: .9rem;
        font-size: .6rem;
        color: #333;
    }
    .address_ul_li_s1{
        display: inline-block;
        font-size: .6rem;
        color: #333;
    }
    .deletesite{
        display: flex;
        align-items: center;
        color: gray;
        font-size: .6rem;
    }
    .addsite{
        margin-top: .4rem;
        background: #fff;
        padding: .2rem .4rem;
        border-top: 1px solid #d9d9d9;
        display: flex;
        justify-content: space-between;
    }
    .addsite_s1{
        display: block;
        font-size: .7rem;
        color: #333;
        line-height: 1.4rem;
    }
    .addsite_s2{
        width: .66667rem;
        height: 1.4rem;
        display: block;
        font-size: .7rem;
        color: #333;
        line-height: 1.4rem;
    }
</style>